"use client";
import { useState } from 'react';
import { Dialog, DialogContent, DialogTrigger, DialogHeader, DialogTitle } from '@/components/ui/dialog';
import { Button } from '@/components/ui/button';
import { Label } from '@/components/ui/label';
import { RadioGroup, RadioGroupItem } from '@/components/ui/radio-group';
import { toast } from 'sonner';
import { submitPsychTest } from '@/app/actions';
import { AlertTriangle } from 'lucide-react';

// ✅ 新增 onCrisis 属性
export default function PsychTestModal({ appointmentId, onSuccess, onCrisis }: { 
    appointmentId: string, 
    onSuccess: () => void,
    onCrisis: () => void 
}) {
    const [open, setOpen] = useState(false);
    const [step, setStep] = useState(1);
    const [answers, setAnswers] = useState<{sds: number[], sas: number[], ses: number[]}>({ sds: [], sas: [], ses: [] });

    // ... (中间的题目定义和 handleAnswer 保持不变) ...
    // 简化的题目 (实际项目请替换为完整量表)
    const sdsQuestions = ["我感到情绪低落", "我觉得早上最好", "我经常哭泣"];
    const sasQuestions = ["我感到比平时容易紧张", "我无缘无故感到害怕", "我容易心烦意乱"];
    const sesQuestions = ["我感到我有许多优点", "我希望能以此尊重自己", "总的来说，我倾向于认为自己失败"];

    const handleAnswer = (scale: 'sds'|'sas'|'ses', index: number, val: string) => {
        const newArr = [...answers[scale]];
        while(newArr.length <= index) newArr.push(0);
        newArr[index] = parseInt(val);
        setAnswers({ ...answers, [scale]: newArr });
    };

    const handleSubmit = async () => {
        const res = await submitPsychTest(appointmentId, answers);
        
        if (res.success) {
            setOpen(false);
            onSuccess(); // 刷新列表

            // ✅ 核心逻辑：如果是高风险，提醒并触发弹窗
            if (res.isHighRisk) {
                toast.error(
                    <div className="flex flex-col gap-1">
                        <div className="font-bold flex items-center gap-2">
                            <AlertTriangle className="w-4 h-4" /> 心理预警提示
                        </div>
                        <div>您的测评结果显示心理压力较大。</div>
                        <div className="font-bold text-yellow-200">系统已自动为您打开绿色通道，请立即预约咨询。</div>
                    </div>,
                    { duration: 5000 }
                );
                // 调用父组件的方法，去打开预约窗口
                onCrisis();
            } else {
                toast.success("测评提交成功，感谢您的配合。");
            }
        } else {
            toast.error("提交失败");
        }
    };

    return (
        <Dialog open={open} onOpenChange={setOpen}>
            {/* ... (Trigger 和 Content 部分保持不变，注意把 handleSubmit 绑定到最后的按钮) ... */}
            <DialogTrigger asChild>
                <Button className="bg-orange-500 hover:bg-orange-600 text-white font-bold border-2 border-orange-200">
                    开始心理前测
                </Button>
            </DialogTrigger>
            <DialogContent className="max-w-xl max-h-[80vh] overflow-y-auto">
                <DialogHeader>
                    <DialogTitle>心理状态前测 (步骤 {step}/3)</DialogTitle>
                </DialogHeader>
                
                <div className="space-y-6 py-4">
                    {/* ... (SDS 部分) ... */}
                    {step === 1 && (
                        <div className="space-y-4 animate-in fade-in slide-in-from-right-4">
                            <h3 className="font-bold text-lg border-b pb-2 text-blue-700">SDS 抑郁自评量表</h3>
                            {sdsQuestions.map((q, i) => (
                                <div key={i} className="space-y-2 bg-slate-50 p-4 rounded-lg">
                                    <Label className="text-base">{i+1}. {q}</Label>
                                    <RadioGroup onValueChange={(v) => handleAnswer('sds', i, v)} className="flex gap-6 mt-2">
                                        {[1,2,3,4].map(v => (
                                            <div key={v} className="flex items-center space-x-2">
                                                <RadioGroupItem value={v.toString()} id={`sds-${i}-${v}`} />
                                                <Label htmlFor={`sds-${i}-${v}`}>{v}分</Label>
                                            </div>
                                        ))}
                                    </RadioGroup>
                                </div>
                            ))}
                            <Button onClick={() => setStep(2)} className="w-full mt-4">下一步 (进入SAS)</Button>
                        </div>
                    )}

                    {/* ... (SAS 部分) ... */}
                    {step === 2 && (
                        <div className="space-y-4 animate-in fade-in slide-in-from-right-4">
                            <h3 className="font-bold text-lg border-b pb-2 text-green-700">SAS 焦虑自评量表</h3>
                            {sasQuestions.map((q, i) => (
                                <div key={i} className="space-y-2 bg-slate-50 p-4 rounded-lg">
                                     <Label className="text-base">{i+1}. {q}</Label>
                                     <RadioGroup onValueChange={(v) => handleAnswer('sas', i, v)} className="flex gap-6 mt-2">
                                         {[1,2,3,4].map(v => (
                                            <div key={v} className="flex items-center space-x-2">
                                                <RadioGroupItem value={v.toString()} id={`sas-${i}-${v}`} />
                                                <Label htmlFor={`sas-${i}-${v}`}>{v}分</Label>
                                            </div>
                                        ))}
                                     </RadioGroup>
                                </div>
                            ))}
                            <div className="flex gap-4 pt-4">
                                <Button variant="outline" onClick={() => setStep(1)}>上一步</Button>
                                <Button onClick={() => setStep(3)} className="flex-1">下一步 (进入SES)</Button>
                            </div>
                        </div>
                    )}

                    {/* ... (SES 部分) ... */}
                    {step === 3 && (
                        <div className="space-y-4 animate-in fade-in slide-in-from-right-4">
                            <h3 className="font-bold text-lg border-b pb-2 text-purple-700">SES 自尊量表</h3>
                            {sesQuestions.map((q, i) => (
                                <div key={i} className="space-y-2 bg-slate-50 p-4 rounded-lg">
                                     <Label className="text-base">{i+1}. {q}</Label>
                                     <RadioGroup onValueChange={(v) => handleAnswer('ses', i, v)} className="flex gap-6 mt-2">
                                         {[1,2,3,4].map(v => (
                                            <div key={v} className="flex items-center space-x-2">
                                                <RadioGroupItem value={v.toString()} id={`ses-${i}-${v}`} />
                                                <Label htmlFor={`ses-${i}-${v}`}>{v}分</Label>
                                            </div>
                                        ))}
                                     </RadioGroup>
                                </div>
                            ))}
                            <div className="flex gap-4 pt-4">
                                <Button variant="outline" onClick={() => setStep(2)}>上一步</Button>
                                <Button onClick={handleSubmit} className="flex-1 bg-green-600 hover:bg-green-700">提交所有测评</Button>
                            </div>
                        </div>
                    )}
                </div>
            </DialogContent>
        </Dialog>
    );
}