"use client";
import { useState, useEffect } from 'react';
import { useAppStore } from '@/lib/store';
import { Button } from '@/components/ui/button';
import { Calendar } from '@/components/ui/calendar';
import { Dialog, DialogContent, DialogTrigger, DialogHeader, DialogTitle } from '@/components/ui/dialog';
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from '@/components/ui/select';
import { Input } from '@/components/ui/input';
import { Label } from '@/components/ui/label';
import { Textarea } from '@/components/ui/textarea';
import { Badge } from '@/components/ui/badge';
import { Tabs, TabsContent, TabsList, TabsTrigger } from '@/components/ui/tabs';
import { toast } from 'sonner';
import { ClipboardList, History, Loader2, User } from 'lucide-react';
import { getCounselors, createAppointmentWithDetails, getAppointments, getCounselorSchedule } from '@/app/actions';
import PsychTestModal from '@/components/PsychTestModal';

export default function BookingPage() {
  const { currentUser } = useAppStore();
  const [activeTab, setActiveTab] = useState("new");
  
  const [counselors, setCounselors] = useState<any[]>([]);
  const [myAppointments, setMyAppointments] = useState<any[]>([]);
  
  // 预约逻辑状态
  const [selectedDate, setSelectedDate] = useState<Date | undefined>(new Date());
  const [selectedTime, setSelectedTime] = useState<string>("");
  const [availableSlots, setAvailableSlots] = useState<string[]>([]);
  
  // ✅ 核心控制：targetCounselorId 控制哪个弹窗打开
  const [targetCounselorId, setTargetCounselorId] = useState<string | null>(null);
  const [loadingSlots, setLoadingSlots] = useState(false);
  
  // 详细表单状态
  const [formData, setFormData] = useState({
    idCard: '', studentIdNum: '', phone: '', college: '', major: '', 
    grade: '', classInfo: '', mentalHistory: '无', physicalState: '良好', problemType: ''
  });

  const loadData = () => {
     if (currentUser?.id) {
      getCounselors().then(setCounselors);
      getAppointments(currentUser.id, 'student').then(setMyAppointments);
    }
  };

  useEffect(() => { loadData() }, [currentUser, activeTab]);

  // 查排班逻辑
  useEffect(() => {
    async function fetchSlots() {
      if (!targetCounselorId || !selectedDate) return;
      setLoadingSlots(true);
      setSelectedTime("");
      
      let dayOfWeek = selectedDate.getDay(); 
      if (dayOfWeek === 0) dayOfWeek = 7; 

      const res = await getCounselorSchedule(targetCounselorId);
      if (res.success) {
        const slotsForToday = res.data
          .filter((s: any) => s.dayOfWeek === dayOfWeek)
          .map((s: any) => s.timeSlot).sort();
        setAvailableSlots(slotsForToday);
      }
      setLoadingSlots(false);
    }
    fetchSlots();
  }, [targetCounselorId, selectedDate]);


  const handleInputChange = (field: string, value: string) => {
    setFormData(prev => ({ ...prev, [field]: value }));
  };

  // ✅ 关键新增：处理危机预警的回调函数
  const handleCrisisTrigger = () => {
      // 1. 切换到预约Tab
      setActiveTab("new");
      
      // 2. 自动打开第一个咨询师的弹窗
      if (counselors.length > 0) {
          const firstCounselor = counselors[0];
          setTargetCounselorId(firstCounselor.id);
          
          // 3. 预填信息，提示学生
          setFormData(prev => ({
              ...prev,
              problemType: '危机干预', // 自动选择危机干预
              physicalState: '心理测评提示高风险，系统自动引导预约'
          }));
      }
  };

  const handleBook = async () => {
    if (!selectedDate || !selectedTime || !formData.idCard || !formData.phone || !formData.problemType) {
      toast.error("请完整填写所有必填项（包括问题类型）");
      return;
    }

    const res = await createAppointmentWithDetails({
      studentId: currentUser!.id,
      counselorId: targetCounselorId,
      date: selectedDate.toISOString(),
      time: selectedTime,
      ...formData
    });

    if (res.success) {
      toast.success("申请已提交！请等待咨询师确认。");
      setActiveTab("history");
      setTargetCounselorId(null);
      setFormData({
        idCard: '', studentIdNum: '', phone: '', college: '', major: '', 
        grade: '', classInfo: '', mentalHistory: '无', physicalState: '良好', problemType: ''
      });
    } else {
      toast.error(res.msg || "预约失败");
    }
  };

  return (
    <div className="space-y-6">
      <div className="flex items-center justify-between">
          <h1 className="text-2xl font-bold">心理咨询预约</h1>
      </div>

      <Tabs value={activeTab} onValueChange={setActiveTab}>
        <TabsList className="grid w-full grid-cols-2 lg:w-[400px]">
          <TabsTrigger value="new">发起预约</TabsTrigger>
          <TabsTrigger value="history">我的记录</TabsTrigger>
        </TabsList>

        <TabsContent value="new" className="mt-6">
          <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
             {counselors.map((c) => (
                <div key={c.id} className="bg-white p-6 rounded-xl border hover:shadow-md transition-shadow flex flex-col gap-4">
                  <div className="flex items-center gap-4">
                     <div className="w-14 h-14 bg-slate-200 rounded-full flex items-center justify-center overflow-hidden">
                        {c.avatar ? <img src={c.avatar} className="w-full h-full object-cover"/> : <User className="text-slate-400"/>}
                     </div>
                     <div>
                         <h3 className="font-bold text-lg">{c.name}</h3>
                         <p className="text-sm text-slate-500">{c.college || '心理咨询中心'}</p>
                     </div>
                  </div>
                  
                  {/* ✅ Dialog 受控模式：通过 open 属性精确控制显隐 */}
                  <Dialog 
                      open={targetCounselorId === c.id} 
                      onOpenChange={(open) => setTargetCounselorId(open ? c.id : null)}
                  >
                    <DialogTrigger asChild>
                        <Button className="w-full bg-[#5D9C59] hover:bg-[#4a8546]">立即预约</Button>
                    </DialogTrigger>
                    
                    <DialogContent className="max-w-3xl max-h-[90vh] overflow-y-auto">
                      <DialogHeader>
                          <DialogTitle className="text-xl flex items-center gap-2">
                              预约申请表 ({c.name} 老师)
                              {formData.problemType === '危机干预' && <Badge className="bg-red-100 text-red-600 border-0">绿色通道</Badge>}
                          </DialogTitle>
                      </DialogHeader>
                      
                      <div className="grid gap-6 py-4">
                        {/* 1. 基本信息 */}
                        <div className="bg-slate-50 p-4 rounded-lg space-y-4">
                             <div className="grid grid-cols-2 gap-4">
                                <div className="space-y-2"><Label>姓名</Label><Input value={currentUser?.name} disabled className="bg-slate-100"/></div>
                                <div className="space-y-2"><Label>学号 *</Label><Input placeholder="必填" onChange={e=>handleInputChange('studentIdNum', e.target.value)} value={formData.studentIdNum}/></div>
                                <div className="space-y-2"><Label>身份证号 *</Label><Input placeholder="必填" onChange={e=>handleInputChange('idCard', e.target.value)} value={formData.idCard}/></div>
                                <div className="space-y-2"><Label>手机号 *</Label><Input placeholder="必填" onChange={e=>handleInputChange('phone', e.target.value)} value={formData.phone}/></div>
                                <div className="space-y-2"><Label>学院</Label><Input onChange={e=>handleInputChange('college', e.target.value)} value={formData.college}/></div>
                                <div className="space-y-2"><Label>专业</Label><Input onChange={e=>handleInputChange('major', e.target.value)} value={formData.major}/></div>
                                <div className="space-y-2"><Label>年级</Label><Input onChange={e=>handleInputChange('grade', e.target.value)} value={formData.grade}/></div>
                                <div className="space-y-2"><Label>班级</Label><Input onChange={e=>handleInputChange('classInfo', e.target.value)} value={formData.classInfo}/></div>
                            </div>
                        </div>

                        {/* 2. 咨询详情 */}
                        <div className="bg-slate-50 p-4 rounded-lg space-y-4">
                            <h4 className="font-bold text-sm text-slate-700 border-b pb-2">2. 咨询详情</h4>
                            <div className="space-y-2">
                                <Label>咨询问题类型 *</Label>
                                <Select value={formData.problemType} onValueChange={v=>handleInputChange('problemType', v)}>
                                    <SelectTrigger><SelectValue placeholder="请选择" /></SelectTrigger>
                                    <SelectContent>
                                        <SelectItem value="学业压力">学业压力</SelectItem>
                                        <SelectItem value="人际关系">人际关系</SelectItem>
                                        <SelectItem value="情感问题">情感问题</SelectItem>
                                        <SelectItem value="情绪困扰">情绪困扰</SelectItem>
                                        <SelectItem value="生涯规划">生涯规划</SelectItem>
                                        <SelectItem value="危机干预">危机干预</SelectItem>
                                        <SelectItem value="其他">其他</SelectItem>
                                    </SelectContent>
                                </Select>
                            </div>
                            <div className="space-y-2">
                                <Label>个人及家庭精神疾病史</Label>
                                <Textarea value={formData.mentalHistory} placeholder="如有，请简述；如无，请填'无'" onChange={e=>handleInputChange('mentalHistory', e.target.value)} />
                            </div>
                            <div className="space-y-2">
                                <Label>目前身体及心理状态</Label>
                                <Textarea value={formData.physicalState} placeholder="简述目前的睡眠、饮食、情绪状态..." onChange={e=>handleInputChange('physicalState', e.target.value)} />
                            </div>
                        </div>

                        {/* 3. 时间选择 */}
                        <div className="bg-slate-50 p-4 rounded-lg space-y-4">
                            <h4 className="font-bold text-sm text-slate-700 border-b pb-2">3. 预约时间</h4>
                            <div className="flex flex-col md:flex-row gap-6">
                                <div className="border rounded-md bg-white p-2 flex justify-center">
                                    <Calendar mode="single" selected={selectedDate} onSelect={setSelectedDate} 
                                      disabled={(date) => date < new Date(new Date().setHours(0,0,0,0))}
                                    />
                                </div>
                                <div className="flex-1 space-y-4">
                                    <Label>选择可用时段</Label>
                                    {loadingSlots ? (
                                        <div className="flex items-center gap-2 text-slate-500"><Loader2 className="animate-spin w-4 h-4"/> 查询中...</div>
                                    ) : availableSlots.length > 0 ? (
                                        <div className="grid grid-cols-2 gap-2">
                                            {availableSlots.map(slot => (
                                                <Button key={slot} variant={selectedTime === slot ? 'default' : 'outline'} 
                                                    className={selectedTime === slot ? 'bg-[#5D9C59]' : ''}
                                                    onClick={() => setSelectedTime(slot)}>
                                                    {slot}
                                                </Button>
                                            ))}
                                        </div>
                                    ) : (
                                        <p className="text-red-500 text-sm">该日期暂无排班，请切换日期。</p>
                                    )}
                                </div>
                            </div>
                        </div>

                        <Button onClick={handleBook} disabled={!selectedTime} className="w-full bg-[#5D9C59] h-12 text-lg">
                            提交预约申请
                        </Button>
                      </div>
                    </DialogContent>
                  </Dialog>
                </div>
             ))}
          </div>
        </TabsContent>

        <TabsContent value="history" className="mt-6">
          <div className="space-y-4">
            {myAppointments.map((apt) => (
              <div key={apt.id} className="p-5 bg-white border rounded-xl shadow-sm flex flex-col md:flex-row justify-between items-center gap-4">
                <div className="flex items-center gap-4 w-full md:w-auto">
                  <div className="bg-slate-100 p-3 rounded-full"><History className="text-slate-500 w-6 h-6"/></div>
                  <div>
                    <div className="font-bold text-lg text-slate-800">{apt.counselor.name} 老师</div>
                    <div className="text-sm text-slate-500 flex items-center gap-2 mt-1">
                       <Badge variant="outline">{apt.problemType || '常规咨询'}</Badge>
                       <span>{new Date(apt.date).toLocaleDateString()} {apt.time}</span>
                    </div>
                  </div>
                </div>
                
                <div className="flex items-center gap-3 w-full md:w-auto justify-end">
                  {apt.status === 'pending' && <Badge className="bg-blue-100 text-blue-700 hover:bg-blue-200 border-0 px-3 py-1">待审批</Badge>}
                  {apt.status === 'rejected' && <Badge variant="destructive" className="px-3 py-1">已拒绝</Badge>}
                  
                  {/* ✅ 传递 handleCrisisTrigger 给测评组件 */}
                  {apt.status === 'confirmed' && !apt.testResult && (
                      <div className="flex flex-col items-end gap-1">
                          <span className="text-xs text-orange-600 font-bold animate-pulse">审批通过，请完成前测</span>
                          <PsychTestModal 
                              appointmentId={apt.id} 
                              onSuccess={loadData} 
                              onCrisis={handleCrisisTrigger} // 绑定危机处理函数
                          />
                      </div>
                  )}

                  {apt.status === 'confirmed' && apt.testResult && (
                      <div className="flex items-center gap-2">
                          <Badge className="bg-green-100 text-green-700 hover:bg-green-200 border-0 px-3 py-1 flex gap-1 items-center">
                              <ClipboardList className="w-3 h-3"/> 测评已完成
                          </Badge>
                      </div>
                  )}
                </div>
              </div>
            ))}
          </div>
        </TabsContent>
      </Tabs>
    </div>
  );
}