// ==========================================
// 4. 心理测评系统 (TestResult)
// ==========================================

export async function submitPsychTest(appointmentId: string, answers: any) {
  // 1. 计算分数
  const calculateScore = (arr: number[]) => arr.reduce((a, b) => a + b, 0);
  const sdsScore = calculateScore(answers.sds || []);
  const sasScore = calculateScore(answers.sas || []);
  const sesScore = calculateScore(answers.ses || []);

  // 2. 预警判断 (阈值 > 60)
  let isHighRisk = false;
  
  if (sdsScore > 60 || sasScore > 60) {
    isHighRisk = true;
  }

  try {
    // 3. 保存测评结果
    await prisma.testResult.create({
      data: {
        appointmentId,
        rawAnswers: answers,
        sdsScore,
        sasScore,
        sesScore
      }
    });

    // 4. 如果是高风险，仅做标记
    if (isHighRisk) {
        // A. 标记当前预约为危机
        await prisma.appointment.update({
            where: { id: appointmentId },
            data: { isCrisis: true }
        });

        // B. 标记学生档案为红色预警
        const currentApt = await prisma.appointment.findUnique({ where: { id: appointmentId } });
        if (currentApt) {
            await prisma.user.update({
                where: { id: currentApt.studentId },
                data: { riskLevel: 'red' }
            });
        }
    }

    revalidatePath('/student/booking');
    revalidatePath('/counselor/dashboard');
    
    // ✅ 移除自动预约代码，仅返回风险状态
    return { success: true, isHighRisk };

  } catch (error) {
    console.error("测评提交失败:", error);
    return { success: false, error: "提交失败" };
  }
}

// ==========================================
// 5. 排班系统 (Schedule)
// ==========================================

export async function getCounselorSchedule(counselorId: string) {
  try {
    const schedules = await prisma.schedule.findMany({
      where: { counselorId, isAvailable: true }
    });
    return { success: true, data: schedules };
  } catch (error) {
    return { success: false, data: [] };
  }
}

export async function updateSchedule(counselorId: string, slots: any[]) {
  try {
    const ops = slots.map(slot => 
      prisma.schedule.upsert({
        where: {
          counselorId_dayOfWeek_timeSlot: {
            counselorId,
            dayOfWeek: slot.dayOfWeek,
            timeSlot: slot.timeSlot
          }
        },
        update: { isAvailable: slot.isAvailable },
        create: {
          counselorId,
          dayOfWeek: slot.dayOfWeek,
          timeSlot: slot.timeSlot,
          isAvailable: slot.isAvailable
        }
      })
    );
    
    await prisma.$transaction(ops);
    return { success: true };
  } catch (error) {
    console.error(error);
    return { success: false, error: "保存失败" };
  }
}

// ==========================================
// 6. 聊天系统 (Messages)
// ==========================================

export async function getChatContacts(currentUserId: string, role: string) {
  try {
    if (role === 'student') {
      const counselors = await prisma.user.findMany({
        where: { role: 'counselor' },
        select: { id: true, name: true, avatar: true, college: true }
      });
      return { success: true, data: counselors };
    } else {
      const students = await prisma.user.findMany({
        where: { role: 'student' },
        select: { id: true, name: true, avatar: true, college: true }
      });
      return { success: true, data: students };
    }
  } catch (error) {
    return { success: false, data: [] };
  }
}

export async function getMessages(userId1: string, userId2: string) {
  try {
    const messages = await prisma.message.findMany({
      where: {
        OR: [
          { senderId: userId1, receiverId: userId2 },
          { senderId: userId2, receiverId: userId1 }
        ]
      },
      orderBy: { createdAt: 'asc' },
      include: {
        sender: { select: { name: true, avatar: true } }
      }
    });
    return { success: true, data: messages };
  } catch (error) {
    return { success: false, data: [] };
  }
}

export async function sendMessage(senderId: string, receiverId: string, content: string) {
  try {
    const message = await prisma.message.create({
      data: { senderId, receiverId, content }
    });
    return { success: true, data: message };
  } catch (error) {
    return { success: false, error: "发送失败" };
  }
}

